@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
    margin: 0;
  }
  
  .App {
    background-color: #f9f9f9;
    color: #212121;
    font-family: "Roboto", sans-serif;
  }
.button {
    padding: 8px 8px;
    font-weight: bold;
    background-color: crimson;
    color: white;
    font-size: 12px;
    text-transform: uppercase;
    text-decoration: none;
    display:block;        
    margin:10px auto;
    width: 100px;
  }
  
