  .button:hover {
    background-color: rgb(236, 221, 86);
  }
h1 {
  letter-spacing: -0.5px; /* to make look closer to the original YouTube font */
}

h1,
p {
  margin-top: 0;
}

.search-form input {
    box-sizing: border-box;
    display: inline-block;
    width: calc(100% - 64px - 10px); /* 48px is the width of the search button */
    height: 36px;
    padding: 20px;
    font-size: 14px;
    border: solid 0.5px #f5d95b;
    outline: none;
  }
  
  .search-form a {
    box-sizing: border-box;
    display: inline-block;
    width: 200px;
    height: 50px;
    background-color: #d43131;
    color: #141816;
    border: solid 0.5px #cc2727;
    text-align: center;
    vertical-align: top;
    text-decoration: none;
    line-height: 36px;
    margin-top:30px;
  }
.search-form {
    background-color: whitesmoke;
    background: url(/static/media/home-image3.868577ee.jpg) center;
    background-size: cover;
    height: 580px;
    padding-left: 5px;
}


.card {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: default;
    width: 360px;
    border: 2px solid gray;
    padding: 0px;
    margin: 5px;
    background: url(data:image/jpeg;base64,) center;
    background-size: cover;
  }
  
  .card .card-item {
    color: white;
    font-weight: bold;
    padding: 20px;
    text-align: center;
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0, 0.4);
  }

  /* Desktop */
@media only screen and (min-width: 750px) {
    .card {
      padding: 0px;
      margin-top: 15px;
      margin-bottom: 15px;
      margin-left: auto;
      margin-right: auto;
    }
  }
.header {
    border-top: 56px solid #c32b23;
    padding: 64px 16px;
    background-color: #f9cc46;
    display: grid;
    grid-template-columns: 1fr 1fr; /* each fr is a equal space column. */
    grid-column-gap: 12px;
  }
  
  .header img {
    max-width: 220px;
    height: auto;
    vertical-align: -webkit-baseline-middle;
  }
  
  @media only screen and (min-width: 660px) {
    .header {
      grid-template-columns: 1fr 1fr 1fr; /* each fr is a equal space column. */
    }
  }
.footer {
    background-color: #c32b23;
    display: flex;
    justify-content: space-around;
  }

  .footer a {
      padding: 38px;
      color: white;
      font-weight: bold;
      
  }
.home-page {
  background-color: whitesmoke;
  background: url(/static/media/home-image3.868577ee.jpg) center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  height: 750px;
  text-align: center; 
  
}

/* Desktop */
@media only screen and (min-width: 750px) {
  .home-page {
      height: 1080px; 
  }
}

